@font-face {
  font-family: 'IBMVGA8';
  src: local('Web437_IBM_VGA_8x14'), url('./font/Web437_IBM_VGA_8x14.woff') format('woff');
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: #96daff;
  font-family: IBMVGA8 ,monospace;
}

code {
  font-family: source-code-pro, IBMVGA8, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  text-align: center;
  color: #ffd700;
}

input {
  padding: 5px;
  margin-bottom: 10px;
  width: 100%;
  height: 30px;
  border: 1px solid black;
  background-color: transparent;
}
